import React from 'react'
import { StaticQuery, graphql } from 'gatsby'
import ProgressiveImage from 'react-progressive-image'
import { heavy } from '../components/colors'

import Layout from '../components/layout'
import SEO from '../components/seo'
import HeaderImagePlaceFolder from '../components/headerImagePlaceHolder'
import styled from 'styled-components';

import { makeStyles } from '@material-ui/core/styles';
import Card from '@material-ui/core/Card';
import CardActions from '@material-ui/core/CardActions';
import CardContent from '@material-ui/core/CardContent';
import Typography from '@material-ui/core/Typography';
import Button from '@material-ui/core/Button';

const useStyles = makeStyles({
  root: {
    minWidth: 275,
    marginBottom: 24,
  },
  bullet: {
    display: 'inline-block',
    margin: '0 2px',
    transform: 'scale(0.8)',
  },
  title: {
    fontSize: 14,
  },
  pos: {
    marginBottom: 16,
  },
  footer: {
    paddingLeft: 12,
    paddingRight: 12,
    paddingBottom: 16,
  }
});

const Title = styled.h1`
  color: ${heavy};
  font-size: 22px;
  font-weight: 500;
`;

const CardContainer = styled.div`
  margin-bottom: 32px;
`;

const MoreLinks = styled.a`
  color: ${heavy};
  font-size: 16px;
`;

function PoloDBProjects() {
  const classes = useStyles();
  
  function more() {
    window.open('https://github.com/vincentdchan/PoloDB');
  }

  return (
    <Card className={classes.root} variant="outlined">
      <CardContent>
        <Typography variant="h5" component="h2">
          PoloDB
        </Typography>
        <Typography className={classes.pos} color="textSecondary">
          一个嵌入式的 NoSQL 数据库
        </Typography>
        <Typography variant="body2" component="p">
          主打轻量级、跨平台、可移植，可以像 SQLite 一样嵌入在你的程序里面。
          <br />
          有着类似 MongoDB 的 API，易学易用。
          功能齐全、一致性好。
          <br />
          支持各种语言绑定，包括 Python/Node.js/C/C++/Swift 等。
        </Typography>
      </CardContent>
      <CardActions className={classes.footer}>
        <Button size="small" onClick={more}>Github 地址</Button>
      </CardActions>
    </Card>
  );
}

function JetpackProjects() {
  const classes = useStyles();
  
  function more() {
    window.open('https://github.com/vincentdchan/jetpack.js');
  }

  function article() {
    window.open('https://diverse.space/2020/04/implement-a-extremely-fast-js-bundler')
  }

  return (
    <Card className={classes.root} variant="outlined">
      <CardContent>
        <Typography variant="h5" component="h2">
          Jetpack.js
        </Typography>
        <Typography className={classes.pos} color="textSecondary">
          一个飞快的 JavaScript 打包压缩工具
        </Typography>
        <Typography variant="body2" component="p">
          C++ 编写，运行飞快。
          编译成可以独立运行的二进制，没有运行时（Node.js）依赖。
          <br />
          完整支持 ECMAScript 2017。
          <br />
          支持 JSX 语法。
          <br />
          友好的错误提示。
        </Typography>
      </CardContent>
      <CardActions className={classes.footer}>
        <Button size="small" onClick={more}>Github 地址</Button>
        <Button size="small" onClick={article}>文章</Button>
      </CardActions>
    </Card>
  );
}

function WebpackProjects() {
  const classes = useStyles();
  
  function more() {
    window.open('https://github.com/vincentdchan/webpack-deep-scope-analysis-plugin');
  }

  function article() {
    window.open('https://diverse.space/2018/05/better-tree-shaking-with-scope-analysis')
  }

  return (
    <Card className={classes.root} variant="outlined">
      <CardContent>
        <Typography variant="h5" component="h2">
          Webpack 深作用域分析 Plugin（停止维护）
        </Typography>
        <Typography className={classes.pos} color="textSecondary">
          一个 JavaScript 深作用域分析插件，可帮助 webpack 进行 tree-shaking
        </Typography>
        <Typography variant="body2" component="p">
          GSoC 2018 年项目，Google 认证通过。
          <br />
          在 Webpack 4 上运行，帮助 tree-shaking。
          <br />
          Webpack 5 已经集成此功能，无需额外安装插件。
        </Typography>
      </CardContent>
      <CardActions className={classes.footer}>
        <Button size="small" onClick={more}>Github 地址</Button>
        <Button size="small" onClick={article}>文章</Button>
      </CardActions>
    </Card>
  );
}

const IndexPage = () => (
  <StaticQuery
    query={graphql`
    query {
      allMarkdownRemark(
        sort: { order: DESC, fields: [frontmatter___date] }
        limit: 1000
      ) {
        edges {
          node {
            excerpt(pruneLength: 160, truncate: true)
            frontmatter {
              title
              date
              path
              permalink
            }
          }
        }
      }
    }
    `}
    render={data => (
      <Layout
        headerImage={
          <ProgressiveImage src="/images/homepage.jpg" placeholder="">
            {(src, loading) => {
              return loading ? <HeaderImagePlaceFolder /> : (
                <img
                  src={src}
                  alt=""
                  style={{
                    margin: '0px',
                  }}
                />
              );
            }}
          </ProgressiveImage>
        }
      >
        <SEO
          title="首页"
          keywords={[
            `blog`,
            `technology`,
            `photography`,
          ]}
        />
        <div>
          <Title>项目 · Projects</Title>
        </div>
        <CardContainer>
          <PoloDBProjects />
          <JetpackProjects />
          <WebpackProjects />
        </CardContainer>
        <div>
          <Title>兴趣 · Interests </Title>
          <ul>
            <li>编译原理</li>
            <li>数据库</li>
            <li>编辑器</li>
          </ul>
        </div>
        <div style={{
          marginBottom: 16,
        }}>
          <Title>文章 · Articles </Title>
          <MoreLinks href="/articles">更多文章...</MoreLinks>
        </div>
      </Layout>
    )}
  />
)

export default IndexPage
